import { AnimatePresence, motion } from 'framer-motion'
import { useEffect, useRef, useState } from 'react'

import { Icon } from '@repo/ui/icon'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useWinNumberStore } from '@repo/ui/store/useWinNumberStore'
import { thousandSeparator } from '@repo/utils/common/common'
import { BetState } from '@repo/utils/types/betstate'
import './WinnerList.scss'

const SPEED_REM_PER_SEC = 4

const WinnerList = () => {
  const gameState = useBettingStore((s) => s.gameState)
  const currencySymbol = useCartStore((s) => s.currencySymbol)
  const rootEmFontSize = useLayoutStore((s) => s.rootEmFontSize)
  const ulRef = useRef<HTMLUListElement>(null)
  const [listDuration, setListDuration] = useState(6)
  const [animationComplete, setAnimationComplete] = useState(false)
  const showWinnerList = useWinNumberStore((s) => s.showWinnerList)
  const winnerList = useWinNumberStore((s) => s.winnerList)

  const winAmount = winnerList.reduce((total, winner) => total + winner.amount, 0)

  const winnerListVariant = {
    containerInitial: {
      opacity: 1,
      transition: { duration: 1, ease: 'easeInOut' },
    },
    listInitial: {
      transform: 'TranslateX(23.75rem)',
      opacity: 1,
      transition: { duration: 0, delay: 2 },
    },
    move: {
      transform: 'TranslateX(-100%)',
      transition: { duration: showWinnerList ? listDuration : 0, ease: 'linear' },
    },
    disappear: {
      opacity: 0,
      transition: { duration: 1, ease: 'easeInOut' },
    },
  }

  useEffect(() => {
    if (ulRef.current) {
      setListDuration(ulRef.current.offsetWidth / (SPEED_REM_PER_SEC * rootEmFontSize) + 23.75 / SPEED_REM_PER_SEC)
      setAnimationComplete(false)
    }
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [winnerList])

  useEffect(() => {
    if (!animationComplete && winnerList.length !== 0) {
      useWinNumberStore.setState({ showWinnerList: true })
    } else {
      useWinNumberStore.setState({ showWinnerList: false })
    }
  }, [animationComplete, winnerList])

  return (
    <AnimatePresence>
      <motion.div
        variants={winnerListVariant}
        initial={{ opacity: 0 }}
        animate={showWinnerList && gameState !== BetState.WheelSpin ? 'containerInitial' : 'disappear'}
        onAnimationComplete={(e) => {
          if (e === 'disappear') {
            useWinNumberStore.setState({ winnerList: [] })
            setAnimationComplete(true)
          }
        }}
        className="maskRight pointer-events-none absolute right-[33%] z-10 flex h-4 w-[32.938rem] items-center overflow-hidden bg-clip-text normal-case"
      >
        <Icon.User className="mr-1 h-4 w-4 shrink-0" />
        <div
          className="mr-1 shrink-0 text-[0.813rem] font-semibold tracking-[-0.016rem]"
          style={{ WebkitTextFillColor: '#D9D9D9' }}
        >
          {winnerList.length} won
        </div>
        <div
          className="shrink-0 text-[0.813rem] font-semibold tracking-[-0.016rem]"
          style={{ WebkitTextFillColor: '#BF9E77' }}
        >
          {`${currencySymbol} ${thousandSeparator(winAmount)}`}
        </div>
        <div className="mr-4 h-4 w-4 shrink-0 border-r-[0.063rem] border-[#ffffff] border-opacity-10"></div>
        <div className="maskLeft relative flex-1">
          <div className="relative overflow-hidden">
            <motion.ul
              ref={ulRef}
              variants={winnerListVariant}
              initial={'listInitial'}
              animate={showWinnerList && winnerList ? 'move' : 'listInitial'}
              onAnimationComplete={(e) => {
                if (e === 'move') {
                  setAnimationComplete(true)
                }
              }}
              className="flex h-[0.875rem] w-fit gap-3"
            >
              {winnerList.map(({ nickname, amount }) => (
                <li
                  key={nickname ?? '' + amount}
                  className="flex items-baseline"
                >
                  <span className="winAmount mr-[0.1rem]">{`${currencySymbol} `}</span>
                  <span className="winAmount">{thousandSeparator(amount)}</span>
                  <span className="winName ml-[0.125rem]">{nickname}</span>
                </li>
              ))}
            </motion.ul>
          </div>
        </div>
      </motion.div>
    </AnimatePresence>
  )
}

export default WinnerList
