import ChatRoom from '@repo/ui/components/menu/common/chat/ChatRoom'
import { Tooltip } from '@repo/ui/components/tooltip/Tooltip'
import { Icon } from '@repo/ui/icon'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { RoomType } from '@repo/ui/store/useChatStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { isSicboGameType } from '@repo/ui/utils/common'
import { cn } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'
import useKeyPress from '@repo/utils/hook/useKeyPress'
import { useClickAway } from '@uidotdev/usehooks'
import { memo, useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import './DesktopChat.scss'

/*
 * Spec:
 * 1. Hovering the full-height chat area or input has text will show 3 latest messages,
 *    including both expired and alive.
 * 2. Un-hovering the full-height chat area and input has no text will hide the 3 latest messages,
 *    with a delay of 3 seconds except for the alive ones.
 * 3. Hovering input or input has text will activate the light underlay and the expand button.
 * 4. Hovering the expand button will enable sneak peek.
 * 5. Clicking the expand button will open the chat in full height.
 * 6. Every incoming new message will have a 24 seconds TTL, and will be visible persistently until expired.
 *    - When hovering the full-height chat area, the countdown will be stopped.
 *    - When input has text, the countdown will be stopped.
 *    - When countdown resumes and remaining time is less than 3 seconds,
 *      the message will be hidden AFTER 3 seconds.
 * 7. When the then 3rd msg becomes the 4th message, it will be translated a lil bit to the top.
 */
function DesktopChat() {
  const { t } = useTranslation()
  const dealer = useBettingStore((state) => state.dealer)
  const gameType = useConfigStore((state) => state.GAME_TYPE)
  const isSicbo = isSicboGameType(gameType)

  const [isInputHover, setIsInputHover] = useState(false)
  const [isExpandHover, setIsExpandHover] = useState(false)
  const [isLayerHover, setIsLayerHover] = useState(false)
  const [isExpandHovered, setIsExpandHovered] = useState(false)
  const [isInputHasText, setIsInputHasText] = useState(false)

  const desktopModal = useLayoutStore((state) => state.desktopModal)
  const isPayoutModalOpen = useLayoutStore((state) => state.isPayoutModalOpen)
  const isChatExpand = useLayoutStore((state) => state.isChatExpand)
  const openDesktopModal = useLayoutStore((state) => state.openDesktopModal)
  const closeDesktopModal = useLayoutStore((state) => state.closeDesktopModal)
  const closeNewestDesktopModal = useLayoutStore((state) => state.closeNewestDesktopModal)

  useKeyPress({
    targetKey: 'Escape',
    callback: () => {
      // Limit the Escape key to only close the chat when it is expanded and no other modal (right-side) is opened
      if (isChatExpand && desktopModal === null) {
        closeNewestDesktopModal()
      }
    },
  })

  const ref = useClickAway<HTMLDivElement>((e) => {
    const targetSide = (e.target as HTMLElement).closest('[data-modal-side]')?.getAttribute('data-modal-side')

    // Only close the newest modal if the click is outside the data-modal-side area, chat is expanded and no other modal (right-side) is opened
    if (!targetSide && isChatExpand && desktopModal === null) closeNewestDesktopModal()
  })

  function handleCollapse() {
    playSound(['click'])
    closeDesktopModal('chat')
    setIsInputHover(false)
    const chatRoomContent = document.querySelector('.chatRoomContent')
    // Scroll to bottom when collapses so that the last messages are visible.
    chatRoomContent?.scrollTo({ top: chatRoomContent.scrollHeight, behavior: 'smooth' })
  }

  useEffect(() => {
    let timeoutId: ReturnType<typeof setTimeout> | null = null

    /*
     * The fourth msg will be translated to the top,
     * once hovering the expand btn, it needs to be reset to the original position instantly.
     * and un-hovering the expand btn, it needs to be translated to the top again which has unnecessary transition.
     * Using isExpandHovered to as a flag to delay the animation of the fourth msg.
     */
    if (isExpandHovered && !isExpandHover) timeoutId = setTimeout(() => setIsExpandHovered(false), 300)

    return () => {
      if (timeoutId) clearTimeout(timeoutId)
    }
  }, [isExpandHovered, isExpandHover])

  return (
    <div
      id="DesktopChatRoom"
      ref={ref}
      className={cn({ isInputHover, isExpandHover, isExpand: isChatExpand, isSicbo, isLayerHover, isExpandHovered, isInputHasText })}
      onMouseEnter={() => setIsLayerHover(true)}
      onMouseLeave={() => setIsLayerHover(false)}
      data-modal-side="left"
    >
      <div className="presenter">
        <div className="flex gap-[0.125rem] text-xs leading-4 tracking-[0.03rem]">
          <div>{t('COMMON.GAME_PRESENTER')}:</div>
          <div className="title">{dealer.name}</div>
        </div>

        <button
          onClick={() => handleCollapse()}
          className="collapseBtn"
        >
          <Icon.Collapse className="h-[0.688rem] w-[0.688rem] cursor-pointer duration-[0.5s]" />
        </button>
      </div>

      <div className="chatRoomWrapper">
        <ChatRoom
          type={RoomType.Live}
          desktopLive
          setIsInputHover={setIsInputHover}
          isLayerHover={isLayerHover}
          isInputHasText={isInputHasText}
          setIsInputHasText={setIsInputHasText}
        />
      </div>

      <div
        className="expandBtnWrapper"
        onMouseEnter={() => setIsInputHover(true)}
        onMouseLeave={() => setIsInputHover(false)}
      >
        <Tooltip
          content={t('COMMON.EXPAND_CHAT')}
          placement="top"
          selected={isChatExpand}
          className="rounded-full"
          animation={{
            initial: { opacity: 0, y: '2.35rem' },
            animate: { opacity: 1, y: 0, x: 0 },
            exit: isChatExpand ? { opacity: 0, y: '2.35rem', x: '-3rem' } : { opacity: 0, y: '2.35rem', x: 0 },
            transition: { duration: 0.35, ease: 'easeOut' },
          }}
        >
          <button
            className="expandBtn"
            onMouseEnter={() => {
              setIsExpandHover(true)
              setIsExpandHovered(true)
            }}
            onMouseLeave={() => setIsExpandHover(false)}
            onClick={() => {
              playSound(['click'])
              openDesktopModal('chat')
              if (isPayoutModalOpen) {
                closeDesktopModal('payout')
              }
            }}
          >
            <Icon.Expand className="h-[1.5rem] w-[1.5rem] cursor-pointer duration-[0.5s] hover:brightness-125" />
          </button>
        </Tooltip>
      </div>
    </div>
  )
}

export default memo(DesktopChat)
