import NetworkStateModal from '@repo/ui/components/common/modal/NetworkStateModal'
import CountDownCap from '@repo/ui/components/countDownCap/CountDownCap'
import Footer from '@repo/ui/components/layout/footer/desktop/Footer'
import Header from '@repo/ui/components/layout/header/desktop/Header'
import MainBgImage from '@repo/ui/components/layout/MainBgImage'
import DesktopChat from '@repo/ui/components/menu/common/chat/desktopChat/DesktopChat'
import DesktopMenu from '@repo/ui/components/menu/desktop/DesktopMenu'
import ToastList from '@repo/ui/components/toast/ToastList'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { getWinNumberStore } from '@repo/ui/store/useWinNumberStore'

import overlayBg from '@roulette/assets/overlay_bg.webp'
import VideoStream from '@roulette/components/video/VideoStream'
import './DesktopLayout.scss'
import { DesktopSquareButton } from './DesktopSquareButton'
import GameContent from './GameContent'

const useWinNumberStore = getWinNumberStore<string>()

// FND-910
const GAME_TABLE_MAP = {
  'ARO-001': 'SPEED AUTO ROULETTE',
  'ARO-002': 'VIP AUTO ROULETTE',
}

const DesktopLayout = () => {
  const lastWinCache = useWinNumberStore((s) => s.lastWinCache)
  const gameTable = useConfigStore((s) => s.GAME_TABLE) as keyof typeof GAME_TABLE_MAP
  const IS_CLOSE_SERVER = useConfigStore((s) => s.IS_CLOSE_SERVER)

  return (
    <div id="DesktopLayout">
      <Header title={GAME_TABLE_MAP[gameTable] ?? 'AUTO ROULETTE'} />
      <div
        id="MainBoard"
        className="relative"
      >
        <MainBgImage
          overlayBg={overlayBg}
          imageClassName="h-full"
        />
        <ToastList></ToastList>
        <CountDownCap />
        <VideoStream />
        <GameContent />
        <DesktopChat />
        <DesktopSquareButton></DesktopSquareButton>
        <NetworkStateModal isCloseServer={IS_CLOSE_SERVER} />
      </div>
      <DesktopMenu />
      <Footer lastWinCache={lastWinCache} />
    </div>
  )
}

export default DesktopLayout
