import { useTranslation } from 'react-i18next'

import SquareButton from '@repo/ui/components/common/button/SquareButton'
import DesktopModal from '@repo/ui/components/common/modal/DesktopModal'
import { Icon } from '@repo/ui/icon'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { cn } from '@repo/utils/common/common'

import { default as DesktopBettingMode } from '@roulette/components/layout/menu/bettingMode/DesktopBettingMode'
import Statistic from '@roulette/components/layout/menu/statistics/Statistic'
import StatisticFooter from '@roulette/components/layout/menu/statistics/StatisticFooter'
import { useStatisticStore } from '@roulette/stores/useStatisticStore'

export const DesktopSquareButton = () => {
  const { t } = useTranslation()
  const desktopModal = useLayoutStore((state: { desktopModal: string | null }) => state.desktopModal)
  const activeTab = useStatisticStore((state) => state.activeTab)
  const openDesktopModal = useLayoutStore((state) => state.openDesktopModal)
  const closeDesktopModal = useLayoutStore((state) => state.closeDesktopModal)
  const betMode = useBettingStore((state) => state.betMode)
  return (
    <>
      {/* Betting Mode */}
      <SquareButton
        child={
          betMode === 'default' ? (
            <Icon.BettingDefault />
          ) : betMode === 'straight' ? (
            <Icon.BettingStraightUp />
          ) : betMode === 'fullComplete' ? (
            <Icon.BettingFullComplete />
          ) : null
        }
        className="absolute bottom-[16.8rem] right-8"
        buttonClassName="rounded-[0.75rem] h-10 w-10"
        tooltipContent={t('ROULETTE.BETTING_MODE')}
        tooltipPlacement="left"
        selected={desktopModal === 'betting_mode'}
        data-modal-side="right"
        onClick={() => {
          if (desktopModal === 'betting_mode') {
            closeDesktopModal(desktopModal)
          } else {
            openDesktopModal('betting_mode')
          }
        }}
      />

      {/* Betting Mode */}
      {desktopModal === 'betting_mode' && (
        <DesktopModal
          position="bottom-[16.2rem] right-[5.65rem] w-[21.48rem] z-20"
          title={t('ROULETTE.BETTING_MODE')}
          modalType="betting_mode"
          requireShadow={false}
          content={<DesktopBettingMode />}
          placement="right"
          modalSide="right"
        />
      )}

      {/* Statistics */}
      <SquareButton
        child={<Icon.StatisticSolid />}
        tooltipPlacement="left"
        tooltipContent={t('COMMON.STATISTICS')}
        className="absolute bottom-[13.55rem] right-8"
        buttonClassName="rounded-[0.75rem] h-10 w-10"
        selected={desktopModal === 'statistics'}
        data-modal-side="right"
        onClick={() => {
          if (desktopModal === 'statistics') {
            closeDesktopModal(desktopModal)
          } else {
            openDesktopModal('statistics')
          }
        }}
      />
      {desktopModal === 'statistics' && (
        <DesktopModal
          position="right-[5.3rem] top-[4rem] w-[22.1rem] h-[32rem]"
          contentWrapClassName="pb-[1.9rem]"
          title={t('COMMON.STATISTICS')}
          modalType="statistics"
          content={
            <div
              className={cn('relative h-[15.6875rem]', {
                'h-[16.6875rem]': activeTab === 'advanced',
              })}
            >
              <Statistic />
              {activeTab === 'last500' && <div className="scrollShadow h-[3.5rem]" />}
              <StatisticFooter />
            </div>
          }
          placement="right"
          modalSide="right"
        />
      )}
    </>
  )
}
