import { useCartStore } from '@repo/ui/store/useCartStore'
import { getChipSrc } from '@repo/ui/store/utils/chips'
import { convertLabel } from '@repo/utils/common/bet'
import { cn } from '@repo/utils/common/common'
import './DesktopChip.scss'

import ClickableArea from '@repo/ui/components/common/clickableArea/ClickableArea'

interface DesktopChipProps {
  val: number
  className?: string
  isActive: boolean
  isDisabled: boolean
}

export const DesktopChip = ({ val, isDisabled, isActive, className }: DesktopChipProps) => {
  const { front } = getChipSrc(val, isDisabled)
  const displayChipVal = convertLabel(val)

  const handleClick = () => {
    if (isDisabled) return
    useCartStore.getState().setSelectChipVal(val)
  }

  return (
    <ClickableArea
      wrapClassName="chipClickableArea"
      className="size-[3.125rem]"
      onClick={handleClick}
    >
      <div
        className={cn('desktopChip', { isActive, isDisabled }, className)}
        style={{ '--mask-image': `url(${front})` } as React.CSSProperties}
      >
        <img
          src={front}
          alt={`Chip value ${val}`}
        />

        {!isDisabled && (
          <>
            {/* FND-1251 */}
            {/* Darker */}
            <div className="strokeOverflowTop">{displayChipVal}</div>
            {/* Lighter */}
            <div className="strokeOverflowBottom">{displayChipVal}</div>
            {/* Main */}
            <div className="stroke">{displayChipVal}</div>
          </>
        )}

        <div className="text">{displayChipVal}</div>
        <div className="chipMask"></div>
      </div>
    </ClickableArea>
  )
}

export default DesktopChip
