import { useTranslation } from 'react-i18next'

import SoundButton from '@repo/ui/components/common/button/SoundButton'
import SettingSlider from '@repo/ui/components/menu/common/setting/elements/Slider'
import { useSettingStore } from '@repo/ui/store/useSettingStore'
import { cn } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'
import './SoundSetting.scss'

interface SoundSettingProps {
  isVolumeControlOpen: boolean
  setIsVolumeControlOpen: (value: boolean) => void
}

const VOLUME_SLIDER_INTERVALS = 44 // For pixel perfect

export default function SoundSetting({ isVolumeControlOpen, setIsVolumeControlOpen }: SoundSettingProps) {
  const { masterVolume, gameVolume, dealerVolume, studioVolume, volumeChange, toggleVolume } = useSettingStore()
  const { t } = useTranslation()

  return (
    <div id="SoundSetting">
      <div className={cn('volumeControlContainer', { open: isVolumeControlOpen })}>
        <SettingSlider
          title={t('COMMON.MASTER_VOLUME')}
          value={masterVolume}
          onChange={(val) => volumeChange('masterVolume', val)}
          className="volumeControl normal-case"
          prefixElement={
            <SoundButton
              level={masterVolume}
              onClick={() => toggleVolume('masterVolume')}
              type="master"
            />
          }
          intervalCount={VOLUME_SLIDER_INTERVALS}
        />
        <div
          className="volumeControlExpand group mt-[0.55rem]"
          onClick={() => {
            playSound(['click'])
            setIsVolumeControlOpen(!isVolumeControlOpen)
          }}
        >
          <span className="text-xs uppercase">
            {isVolumeControlOpen ? t('COMMON.LESS_VOLUME_SETTINGS') : t('COMMON.MORE_VOLUME_SETTINGS')}
          </span>
          <svg
            viewBox="0 0 21 20"
            fill="none"
            className="volumeSettingsArrow mr-[0.15rem] h-5 w-5"
          >
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M5.71967 8.21967C6.01256 7.92678 6.48744 7.92678 6.78033 8.21967L10.5 11.9393L14.2197 8.21967C14.5126 7.92678 14.9874 7.92678 15.2803 8.21967C15.5732 8.51256 15.5732 8.98744 15.2803 9.28033L11.0303 13.5303C10.8897 13.671 10.6989 13.75 10.5 13.75C10.3011 13.75 10.1103 13.671 9.96967 13.5303L5.71967 9.28033C5.42678 8.98744 5.42678 8.51256 5.71967 8.21967Z"
              fill={isVolumeControlOpen ? 'url(#volumeSettingsArrow)' : '#7A7A7A'}
            />
            <defs>
              <linearGradient
                id="volumeSettingsArrow"
                x1="10.5"
                y1="8"
                x2="10.5"
                y2="13.75"
                gradientUnits="userSpaceOnUse"
              >
                <stop stopColor="#ECE9E1" />
                <stop
                  offset="1"
                  stopColor="#AFA483"
                />
              </linearGradient>
            </defs>
          </svg>
        </div>
        <hr
          className={`mb-2 mt-[0.45rem] border-t-[0.0625rem] border-[#434343] ${isVolumeControlOpen ? 'group-hover:visible' : 'invisible'}`}
        />
        <SettingSlider
          title={t('COMMON.STUDIO_SOUNDS')}
          value={studioVolume}
          onChange={(val) => volumeChange('studioVolume', val)}
          className="volumeControl normal-case"
          prefixElement={
            <SoundButton
              level={studioVolume}
              onClick={() => toggleVolume('studioVolume')}
              type="studio"
            />
          }
          intervalCount={VOLUME_SLIDER_INTERVALS}
        />
        <hr className="mb-[0.68rem] mt-[0.73rem] border-t-[0.0625rem] border-[#434343]" />
        <SettingSlider
          title={t('COMMON.GAME_SOUNDS')}
          value={gameVolume}
          onChange={(val) => volumeChange('gameVolume', val)}
          className="volumeControl normal-case"
          prefixElement={
            <SoundButton
              level={gameVolume}
              onClick={() => toggleVolume('gameVolume')}
              type="game"
            />
          }
          intervalCount={VOLUME_SLIDER_INTERVALS}
        />
        <hr className="mb-[0.7rem] mt-[0.8rem] border-t-[0.0625rem] border-[#434343]" />
        <SettingSlider
          title={t('COMMON.DEALERS_VOICE')}
          value={dealerVolume}
          onChange={(val) => volumeChange('dealerVolume', val)}
          className="volumeControl normal-case"
          prefixElement={
            <SoundButton
              level={dealerVolume}
              onClick={() => toggleVolume('dealerVolume')}
              type="dealer"
            />
          }
          intervalCount={VOLUME_SLIDER_INTERVALS}
        />
      </div>
    </div>
  )
}
