import AnimateCounter from '@repo/ui/components/animatedContainer/AnimateCounter'
import { Icon } from '@repo/ui/icon'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { cn } from '@repo/utils/common/common'
import { useState } from 'react'
import './RaceTrackRangeButton.scss'

export default function RaceTrackRangeButton({ isVertical, className }: { isVertical?: boolean; className?: string }) {
  const neighborRange = useBettingStore((state) => state.neighborRange)
  const [direction, setDirection] = useState<'up' | 'down' | null>(null)

  return (
    <div
      id="RaceTrackRangeButton"
      className={cn({ isVertical }, className)}
    >
      <div className={cn('borderInner', { 'flex flex-row-reverse': isVertical })}>
        <div className={cn('iconBtn', { disabled: neighborRange === 9 })}>
          <Icon.Plus
            className="size-full"
            onClick={() => {
              if (neighborRange === 9) return
              useBettingStore.setState({ neighborRange: neighborRange + 1 })
              setDirection('up')
            }}
          />
        </div>
        <div className={cn('relative', isVertical ? 'w-[2rem]' : 'h-[2rem]')}>
          <AnimateCounter
            className="num center absolute size-8"
            value={neighborRange}
            direction={direction}
            distance={15}
            axis={isVertical ? 'x' : 'y'}
            reverseDirection={isVertical}
          />
        </div>
        <div className={cn('iconBtn', { disabled: neighborRange === 0 })}>
          <Icon.Minus
            className="size-full"
            onClick={() => {
              if (neighborRange === 0) return
              useBettingStore.setState({ neighborRange: neighborRange - 1 })
              setDirection('down')
            }}
          />
        </div>
      </div>
    </div>
  )
}
