import { GameAction } from '@ikigaians/connect'
import { JSX, useMemo, useState } from 'react'
import { useTranslation } from 'react-i18next'

import DesktopModal, { type DesktopModalProps } from '@repo/ui/components/common/modal/DesktopModal'
import { GameHistoryContentHeader } from '@repo/ui/components/menu/common/history/GameHistoryContent'
import { RoomType } from '@repo/ui/store/useChatStore'
import { useDataStore } from '@repo/ui/store/useDataStore'
import { useLayoutStore, type DesktopModalType } from '@repo/ui/store/useLayoutStore'
import { connectIns } from '@repo/utils/socket'

import ChatRoom from '@repo/ui/components/menu/common/chat/ChatRoom'
import GameRule from '@repo/ui/components/menu/common/game-rules/GameRule'
import GameRuleInfo from '@repo/ui/components/menu/common/game-rules/GameRuleInfo'
import GameHistory from '@repo/ui/components/menu/common/history/GameHistory'
import GameHistoryContent from '@repo/ui/components/menu/common/history/GameHistoryContent'
import Setting from '@repo/ui/components/menu/common/setting/Setting'
import SettingLanguage from '@repo/ui/components/menu/common/setting/SettingLanguage'
import SoundSetting from '@repo/ui/components/menu/common/sound/SoundSetting'
import { GameRules } from '@repo/ui/utils/componentResolver'
import './DesktopMenu.scss'

type DesktopMenuProps = {
  settingContent?: {
    gameSpecificsElement?: JSX.Element
  }
}

type MenuComponentKey = 'settings' | 'language' | 'history' | 'history_content' | 'help' | 'rules_inner' | 'support' | 'sound'

const useMenuComponents = (
  settingContent?: DesktopMenuProps['settingContent']
): Record<MenuComponentKey, Omit<DesktopModalProps, 'position' | 'placement'>> => {
  const { t } = useTranslation()
  const titleList = useDataStore.getState().titleList
  const [isVolumeControlOpen, setIsVolumeControlOpen] = useState<boolean>(false)

  return useMemo(
    () => ({
      settings: {
        modalType: 'settings',
        title: t('COMMON.SETTINGS'),
        content: <Setting gameSpecificsElement={settingContent?.gameSpecificsElement} />,
        modalSide: 'right',
        scrollWrapClassName: 'pb-11',
      },
      language: {
        modalType: 'language',
        title: t('COMMON.LANGUAGE'),
        content: <SettingLanguage />,
        leftIcon: true,
        modalSide: 'right',
        scrollWrapClassName: 'pb-11',
      },
      history: {
        modalType: 'history',
        title: t('COMMON.GAME_HISTORY'),
        content: <GameHistory />,
        modalSide: 'right',
      },
      history_content: {
        modalType: 'history_content',
        title: <GameHistoryContentHeader />,
        content: <GameHistoryContent />,
        leftIcon: true,
        modalSide: 'right',
        scrollWrapClassName: 'pb-[5.25rem]',
      },
      help: {
        modalType: 'help',
        title: t('COMMON.HELP_RULES'),
        content: <GameRule titleList={titleList} />,
        modalSide: 'right',
        scrollWrapClassName: 'pb-11',
      },
      rules_inner: {
        modalType: 'rules_inner',
        title: t('COMMON.GAMES_RULES'),
        content: (
          <GameRuleInfo>
            <GameRules />
          </GameRuleInfo>
        ),
        leftIcon: true,
        modalSide: 'right',
        scrollWrapClassName: 'pb-11',
      },
      support: {
        modalType: 'support',
        title: t('COMMON.SUPPORT'),
        content: <ChatRoom type={RoomType.Support} />,
        onBeforeCloseModal: () => {
          connectIns.send(GameAction.UNSUBSCRIBE_SUPPORT)
        },
        modalSide: 'right',
      },
      sound: {
        modalType: 'sound',
        title: t('COMMON.SOUND'),
        content: (
          <SoundSetting
            isVolumeControlOpen={isVolumeControlOpen}
            setIsVolumeControlOpen={setIsVolumeControlOpen}
          />
        ),
        requireShadow: false,
        scrollWrapClassName: 'min-h-[7rem]',
        modalSide: 'right',
      },
    }),
    [t, settingContent?.gameSpecificsElement, titleList, isVolumeControlOpen]
  )
}

function isValidMenuKey(key: DesktopModalType, components: Record<MenuComponentKey, unknown>): key is MenuComponentKey {
  if (!key) {
    return false
  }
  return key in components
}

export default function DesktopMenu(props: DesktopMenuProps) {
  const { settingContent } = props

  const desktopModal = useLayoutStore((state) => state.desktopModal)

  const menuComponents = useMenuComponents(settingContent)

  const commonModalProps = {
    // FIXME: Now the height of the sound modal is different from other modals and needs to be scaled according to the content height.
    position:
      desktopModal === 'sound' ? 'right-[0.5rem] top-[4rem] w-[22.5rem] h-fit' : 'right-[0.5rem] top-[4rem] w-[22.5rem] h-[28.43rem]',
    placement: 'right' as const,
  }

  if (!isValidMenuKey(desktopModal, menuComponents)) {
    return null
  }

  const currentModalConfig = menuComponents[desktopModal]

  return (
    <DesktopModal
      {...commonModalProps}
      {...currentModalConfig}
    />
  )
}
