import ClickableArea from '@repo/ui/components/common/clickableArea/ClickableArea'
import { motion } from 'framer-motion'
import { useEffect } from 'react'
import { useTranslation } from 'react-i18next'

import autoplayCloseImg from '@repo/ui/assets/autoplay/autoplayClose.webp'
import autoplayRoundImg from '@repo/ui/assets/autoplay/autoplayRound.webp'
import DesktopModal from '@repo/ui/components/common/modal/DesktopModal'
import AutoPlay from '@repo/ui/components/menu/common/bettingMode/AutoPlay'
import { Icon } from '@repo/ui/icon'
import { tooltipEventBus } from '@repo/ui/store/tooltipEvent'
import { useAutoPlayStore } from '@repo/ui/store/useAutoPlayStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { cn } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'

export const FooterAutoPlay = () => {
  const desktopModal = useLayoutStore((state) => state.desktopModal)
  const openDesktopModal = useLayoutStore((state) => state.openDesktopModal)
  const closeDesktopModal = useLayoutStore((state) => state.closeDesktopModal)
  const isAutoPlayOpen = desktopModal === 'autoplay'

  const isAutoPlay = useAutoPlayStore((state) => state.isAutoPlay)
  const roundRemaining = useAutoPlayStore((state) => state.roundRemaining)
  const isHaveBets = useAutoPlayStore((state) => state.isHaveBets)
  const noBet = !isHaveBets()
  const cancelAutoPlay = useAutoPlayStore((state) => state.cancelAutoPlay)
  const { t } = useTranslation()

  useEffect(() => {
    if (!isAutoPlay) return
    if (noBet) {
      cancelAutoPlay()
    }
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [noBet])

  return (
    <>
      <ClickableArea
        wrapClassName="iconClickableArea"
        className="h-[3.125rem] w-[120%]"
        onClick={() => {
          if (!isHaveBets()) {
            tooltipEventBus.emit('noBet', { anchorSelect: '#AutoPlayBtn' })
            return
          }
          playSound(['click'])
          if (isAutoPlayOpen) {
            closeDesktopModal(desktopModal)
          } else {
            openDesktopModal('autoplay')
          }
        }}
      >
        <div
          id="AutoPlayBtn"
          className={cn(`textBtn pointer-events-none z-[1] flex items-center`, {
            isDisabled: noBet,
            'mr-1': !isAutoPlay,
          })}
          data-modal-side="right"
        >
          {isAutoPlay ? (
            <>
              <img
                src={autoplayRoundImg}
                alt="autoplay round"
                className="mr-[0.14rem] w-8"
              />
              <motion.div
                animate={{ opacity: 1, y: 0 }}
                initial={{ opacity: 0, y: -5 }}
                transition={{
                  type: 'spring',
                  stiffness: 500,
                  damping: 20,
                  mass: 1,
                  duration: 0.3,
                }}
                exit={{ opacity: 0, y: 3 }}
                className="absolute left-[0.55rem] w-4 text-center text-[0.55rem]"
              >
                {roundRemaining}
              </motion.div>
            </>
          ) : noBet ? (
            <Icon.AutoplayDisabled className="mr-[0.275rem] mt-[0.1rem] w-[1.725rem]" />
          ) : (
            <Icon.Autoplay className="mr-[0.47rem] w-[1.375rem]" />
          )}
          <span className="text-[0.875rem]">{t('COMMON.AUTOPLAY')}</span>
          {isAutoPlay && (
            <motion.div
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.9 }}
              className="pointer-events-auto"
              onClick={(e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
                e.stopPropagation()
                cancelAutoPlay()
                playSound(['click'])
              }}
            >
              <img
                src={autoplayCloseImg}
                alt="autoplay close"
                className="w-6"
              />
            </motion.div>
          )}
        </div>
      </ClickableArea>
      {isAutoPlayOpen && !noBet && (
        <DesktopModal
          position="z-16 bottom-16 right-[3.4rem] w-[19rem] z-20"
          contentWrapClassName="px-4 pb-4 pt-1"
          title={t('COMMON.AUTOPLAY')}
          modalType="autoplay"
          requireShadow={false}
          content={
            <AutoPlay
              requireTitle={false}
              closeOnDesktop={() => {
                closeDesktopModal('autoplay')
              }}
            />
          }
          placement="bottom"
          modalSide="right"
        />
      )}
    </>
  )
}
