import { useCartStore } from '@repo/ui/store/useCartStore'
import { isChipActive, isChipDisabled } from '@repo/ui/store/utils/chips'
import './ChipTray.scss'
import DesktopChip from './DesktopChip'

export default function ChipTray() {
  const chipList = useCartStore((s) => s.chipList).filter((chip) => chip.onTable === true)

  return (
    <div
      id="D_ChipTray"
      className="mx-2 flex items-center gap-[1.25rem] px-[.375rem]"
    >
      {chipList.map((chip, idx) => {
        const isActive = isChipActive(chip.amount)
        const isDisabled = isChipDisabled(chip.amount)
        return (
          <DesktopChip
            key={idx}
            val={chip.amount}
            isActive={isActive}
            isDisabled={isDisabled}
            className={isActive ? 'active' : ''}
          />
        )
      })}
    </div>
  )
}
