import ClickableArea from '@repo/ui/components/common/clickableArea/ClickableArea'
import { useLongPress } from '@uidotdev/usehooks'
import { memo } from 'react'
import { useTranslation } from 'react-i18next'

import ChipTray from '@repo/ui/components/chipTray/desktop/ChipTray'
import { FooterAutoPlay } from '@repo/ui/components/layout/footer/desktop/FooterAutoPlay'
import FooterLobby from '@repo/ui/components/layout/footer/desktop/FooterLobby'
import { Tooltip } from '@repo/ui/components/tooltip/Tooltip'
import { Icon } from '@repo/ui/icon'
import { useAutoPlayStore } from '@repo/ui/store/useAutoPlayStore'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { cn, thousandSeparator } from '@repo/utils/common/common'
import { BetState } from '@repo/utils/types/betstate'
import './Footer.scss'

const TotalBet = ({ isLabel = false, lastWinCache }: { isLabel?: boolean; lastWinCache: number }) => {
  const totalBet = useCartStore((s) => s.totalBet)
  const isAutoPlay = useAutoPlayStore((s) => s.isAutoPlay)
  const gameState = useBettingStore((s) => s.gameState)
  const { t } = useTranslation()

  const isAutoPlayWin = isAutoPlay && lastWinCache !== 0 && gameState === BetState.Settlement
  const isShowLastWin = lastWinCache || isAutoPlayWin

  if (isLabel) return (isShowLastWin ? t('COMMON.LAST_WIN') : t('COMMON.TOTAL_BET')) + ':'
  return isShowLastWin ? thousandSeparator(lastWinCache) : thousandSeparator(totalBet)
}

function Footer({ lastWinCache }: { lastWinCache: number }) {
  const { boardHistory, boardHistoryCache, remainedBalance, currencySymbol } = useCartStore((s) => ({
    boardHistory: s.boardHistory,
    boardHistoryCache: s.boardHistoryCache,
    remainedBalance: s.remainedBalance,
    currencySymbol: s.currencySymbol,
  }))
  const { handleDouble, handleRepeat, handleUndo } = useCartStore.getState()

  const pressAttrs = useLongPress(() => useCartStore.setState({ boardHistory: [], totalBet: 0 }), {
    threshold: 1500,
  })

  const cacheSum = boardHistoryCache ? Object.values(boardHistoryCache).reduce((a, b) => a + b, 0) : 0
  const isRepeatDisable = !boardHistoryCache || remainedBalance - cacheSum < 0

  const isOverLimit = useBettingStore((state) => state.isOverLimit)
  const isBet = useBettingStore((state) => state.isBet)

  const { AUTOPLAY_ALLOWED, SHOW_LOBBY_ALLOWED } = useConfigStore((s) => s.JURISDICTION)
  const { t } = useTranslation()

  return (
    <div
      id="D_Footer"
      className="relative flex h-14 flex-shrink-0 items-center px-6"
    >
      <div className="divider mr-[1.12rem] grid border-[#313131] pr-4">
        <span className="pairLabel">{t('COMMON.BALANCE')}:</span>
        <span
          className="pairNumber text-[#f4efe8]"
          id="MainBalance"
        >
          {`${currencySymbol} ${thousandSeparator(remainedBalance)}`}
        </span>
      </div>

      <div className="mr-auto grid">
        <span className="pairLabel">
          <TotalBet
            isLabel={true}
            lastWinCache={lastWinCache}
          />
        </span>
        <span className="pairNumber text-[#C9AE8D]">
          {`${currencySymbol} `}
          <TotalBet lastWinCache={lastWinCache} />
        </span>
      </div>

      <div className="center absolute left-1/2 -translate-x-1/2">
        <Tooltip
          content={t('COMMON.UNDO')}
          placement="top"
        >
          <ClickableArea
            wrapClassName="iconClickableArea"
            className="size-[3.125rem]"
            onClick={handleUndo}
          >
            <div
              className={cn('iconBtn w-[4rem]', { isDisabled: boardHistory.length === 0 || !isBet })}
              {...pressAttrs}
              id="MainUndoBtn"
            >
              <Icon.Undo className="UndoSVG w-5" />
              <div className="hoverShine"></div>
            </div>
          </ClickableArea>
        </Tooltip>

        <ChipTray />
        <Tooltip
          content={boardHistory.length > 0 ? t('COMMON.DOUBLE') : t('COMMON.REPEAT')}
          placement="top"
        >
          <div
            id="doubleRepeatBtn"
            className="w-[4rem]"
          >
            {boardHistory.length > 0 && (
              <ClickableArea
                wrapClassName="iconClickableArea"
                className="size-[3.125rem]"
                onClick={handleDouble}
              >
                <div className={cn('iconBtn', { isDisabled: !isBet || isOverLimit })}>
                  <Icon.Double className="DoubleSVG w-5" />
                  <div className="hoverShine"></div>
                </div>
              </ClickableArea>
            )}
            {boardHistory.length <= 0 && (
              <ClickableArea
                wrapClassName="iconClickableArea"
                className="size-[3.125rem]"
                onClick={handleRepeat}
              >
                <div className={cn('iconBtn', { isDisabled: !isBet || isRepeatDisable })}>
                  <Icon.Repeat className="RepeatSVG w-5" />
                  <div className="hoverShine"></div>
                </div>
              </ClickableArea>
            )}
          </div>
        </Tooltip>
      </div>

      {AUTOPLAY_ALLOWED && <FooterAutoPlay />}

      {AUTOPLAY_ALLOWED && SHOW_LOBBY_ALLOWED && <span className="divider ml-5 mr-6 h-[1.5rem] border-[#313131]"></span>}

      {SHOW_LOBBY_ALLOWED && <FooterLobby />}
    </div>
  )
}

export default memo(Footer)
