import { useTranslation } from 'react-i18next'

import Image from '@repo/ui/components/common/Image'
import { cn } from '@repo/utils/common/common'

import { BetModeType, useBettingStore } from '@repo/ui/store/useBettingStore'
import Default from '@roulette/assets/autoPlay/desktop/default.webp'
import FullComplete from '@roulette/assets/autoPlay/desktop/fullComplete.webp'
import StraightUp from '@roulette/assets/autoPlay/desktop/straightUp.webp'
import './BettingMode.scss'

export default function DesktopBettingMode() {
  const betMode = useBettingStore((s) => s.betMode)
  const { t } = useTranslation()

  const toggleType = (betMode: BetModeType) => {
    useBettingStore.setState({ betMode })
  }

  const getBettingModeImage = (betMode: BetModeType) => {
    if (betMode === 'straight') return StraightUp
    if (betMode === 'fullComplete') return FullComplete
    return Default
  }

  return (
    <div
      id="BettingMode"
      className="px-4 pb-4 pt-3"
    >
      <div className="relative flex h-[6rem] w-[19.5rem]">
        <Image
          src={getBettingModeImage(betMode)}
          alt="Auto Play Mode"
        />

        <div
          className={cn('box left-0 w-[33.5%] hover:brightness-125', { active: betMode === 'default' })}
          onClick={() => toggleType('default')}
        >
          <span>{t('COMMON.MODE_DEFAULT')}</span>
        </div>
        <div
          className={cn('box left-[33%] w-[34.1%] hover:brightness-125', { active: betMode === 'straight' })}
          onClick={() => toggleType('straight')}
        >
          <span>{t('COMMON.MODE_STRAIGHT')}</span>
        </div>
        <div
          className={cn('box right-0 w-[33%] hover:brightness-125', { active: betMode === 'fullComplete' })}
          onClick={() => toggleType('fullComplete')}
        >
          <span>{t('COMMON.MODE_FULL_COMPLETE')}</span>
        </div>
      </div>
    </div>
  )
}
