import { useClickAway } from '@uidotdev/usehooks'
import { motion } from 'framer-motion'
import React from 'react'

import { Icon } from '@repo/ui/icon'
import { DesktopModalType, useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { scrollWrapToTop } from '@repo/ui/utils/misc'
import { cn } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'
import useKeyPress from '@repo/utils/hook/useKeyPress'

import './DesktopModal.scss'

export interface DesktopModalProps {
  position?: string
  contentWrapClassName?: string
  scrollWrapClassName?: string
  title: string | React.ReactNode
  modalType: DesktopModalType
  leftIcon?: boolean
  content?: React.ReactNode
  requireShadow?: boolean
  placement: 'bottom' | 'left' | 'right'
  modalSide: 'left' | 'right'
  onBeforeCloseModal?: () => void
}
interface Motion {
  initial: { x?: number; y?: number; opacity: number }
  animate: { x?: number; y?: number; opacity: number }
}

interface Motions {
  bottom?: Motion
  left?: Motion
  right?: Motion
}

const motions: Motions = {
  right: { initial: { x: 10, opacity: 0 }, animate: { x: 0, opacity: 1 } },
  bottom: { initial: { y: 10, opacity: 0 }, animate: { y: 0, opacity: 1 } },
  left: { initial: { x: -10, opacity: 0 }, animate: { x: 0, opacity: 1 } },
}

export default function DesktopModal(props: DesktopModalProps) {
  const prevDesktopModal = useLayoutStore((state) => state.prevDesktopModal)
  const openDesktopModal = useLayoutStore((state) => state.openDesktopModal)
  const closeDesktopModal = useLayoutStore((state) => state.closeDesktopModal)
  const closeNewestDesktopModal = useLayoutStore((state) => state.closeNewestDesktopModal)

  const { position, contentWrapClassName, scrollWrapClassName, placement } = props
  const { content, title, modalType, modalSide } = props
  const { leftIcon = false, onBeforeCloseModal } = props
  const { requireShadow = true } = props

  const handleClose = () => {
    onBeforeCloseModal?.()
    playSound(['click'])
    closeDesktopModal(modalType)
  }

  const handleReturn = () => {
    playSound(['click'])
    scrollWrapToTop(prevDesktopModal)
    openDesktopModal(prevDesktopModal)
  }

  useKeyPress({
    targetKey: 'Escape',
    callback: () => {
      closeNewestDesktopModal()
    },
  })

  const ref = useClickAway<HTMLDivElement>((e) => {
    const targetSide = (e.target as HTMLElement).closest('[data-modal-side]')?.getAttribute('data-modal-side')

    // Only close the newest modal if the click is outside the data-modal-side area
    if (!targetSide) closeNewestDesktopModal()
  })

  return (
    <motion.div
      ref={ref}
      initial={motions[placement]?.initial}
      animate={motions[placement]?.animate}
      transition={{ duration: 0.1, ease: 'linear' }}
      className={`${position} desktopModal`}
      data-modal-type={modalType}
      data-modal-side={modalSide}
    >
      <div className="flex items-center justify-between px-4 pb-1 pt-4">
        <div className="size-11">
          {leftIcon && (
            <Icon.Left
              className="size-11 cursor-pointer hover:brightness-125"
              onClick={handleReturn}
            />
          )}
        </div>
        <div className="goldenHeader">{title}</div>
        <Icon.Close
          className="size-11 cursor-pointer hover:brightness-125"
          onClick={handleClose}
        />
      </div>

      <div className={cn('contentWrap', contentWrapClassName)}>
        <div className={cn('scrollWrap', scrollWrapClassName)}>{content}</div>
      </div>
      {requireShadow && <div className="scrollShadow pointer-events-none h-[5rem] transition-opacity"></div>}
    </motion.div>
  )
}
