import { Fragment, memo, ReactNode } from 'react'

import { ChipContainer } from '@repo/ui/components/common/chip/ChipContainer'
import TrackerChip from '@repo/ui/components/common/chip/TrackerChip'
import Image from '@repo/ui/components/common/Image'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useWinAnimStore } from '@repo/ui/store/useWinAnimStore'
import { useBettingBoard } from '@repo/utils/hook/useBettingBoard'
import useBoardHighlight from '@repo/utils/hook/useBoardHighlight'
import { BetState } from '@repo/utils/types/betstate'

import grid from '@roulette/assets/board/desktop/grid.webp'
import gridBoardTiltShadow from '@roulette/assets/board/desktop/gridBoardTiltShadow.webp'
import { betGridPrefix, convertRouletteMap, getInitWinHighlight, GRID_HOVER_HIGHLIGHT, GRID_WIN_HIGHLIGHT_FN } from '@roulette/service/bet'
import { ALL_GROUP_MAP } from '@roulette/service/group'
import { BetTypeEnum } from '@roulette/types/betType'

import { BoardIDEnum } from '@repo/ui/utils/bet'
import { cn } from '@repo/utils/common/common'
import { getAddChipParams } from '@roulette/service/board/grid'
import './GridBoard.scss'

interface LatticeProps {
  pos: number[]
  id: string
  className?: string
  children?: ReactNode
  highlights?: string[]
}

const SKEW_MULTIPLIER = 0.4
/** A total of 42 columns, 21.5 is the median of the columns */
const BOARD_COL_COUNT_MEDIAN = 21.5

const Lattice = memo((props: LatticeProps) => {
  const { pos, id, className, children, highlights } = props
  // Extract grid positioning values
  const [rowStart, rowEnd, colStart, colEnd] = pos

  const isHighlighted = highlights?.includes(id) ?? false

  const isRaceTrack = useBettingStore((s) => s.isRaceTrack)
  const isSlope = useBettingStore((s) => !s.isBet) && !isRaceTrack

  const chipSkewXDeg = ((colStart + colEnd) / 2 - BOARD_COL_COUNT_MEDIAN) * -SKEW_MULTIPLIER

  return (
    <span
      id={`${betGridPrefix}${id}`}
      className={cn('center relative', className, isHighlighted && 'highlighted')}
      style={{ gridArea: `-${rowStart} / ${colStart} / -${rowEnd} / ${colEnd}` }}
    >
      {children}
      {/* Use percentage width to fit the size when switching main board */}
      <ChipContainer
        showWin={!isRaceTrack}
        chipId={id}
        chipStyle={cn(`w-[6%] h-[17.5%]`, {
          'text-[76%]': isRaceTrack,
          'text-[114%]': !isRaceTrack,
        })}
        isSlope={isSlope}
        chipSkewXDeg={chipSkewXDeg}
      />
    </span>
  )
})

const StaticGridBoard = memo(({ highlights }: { highlights: string[] }) => {
  const isSettlement = useBettingStore((s) => s.gameState === BetState.Settlement)
  const animState = useWinAnimStore((s) => s.animState)
  const shouldHoverRange = !isSettlement && !animState

  return (
    <div className="StaticGridBoard">
      <div
        id="visibleGridBoard"
        className="visibleBoard contents"
      >
        {shouldHoverRange &&
          Object.entries(GRID_HOVER_HIGHLIGHT).map(([id, ele]) => (
            <span
              key={id}
              className={cn(ele.className, highlights.includes(id) && 'highlighted', 'pointer-events-none')}
              style={{ gridArea: `${ele.pos[0]} / ${ele.pos[1]} / ${ele.pos[2]} / ${ele.pos[3]}` }}
            ></span>
          ))}
        <Lattice
          highlights={highlights}
          pos={[1, 3, 3, 9]}
          id={BetTypeEnum.LOW}
          className="rounded-bl-[0.9em]"
        />
        <Lattice
          highlights={highlights}
          pos={[1, 3, 9, 15]}
          id={BetTypeEnum.EVEN}
          className=""
        />
        <Lattice
          highlights={highlights}
          pos={[1, 3, 15, 21]}
          id={BetTypeEnum.RED}
          className=""
        />
        <Lattice
          highlights={highlights}
          pos={[1, 3, 21, 27]}
          id={BetTypeEnum.BLACK}
          className=""
        />
        <Lattice
          highlights={highlights}
          pos={[1, 3, 27, 33]}
          id={BetTypeEnum.ODD}
          className=""
        />
        <Lattice
          highlights={highlights}
          pos={[1, 3, 33, 39]}
          id={BetTypeEnum.HIGH}
          className="rounded-br-[0.9em]"
        />
        <Lattice
          highlights={highlights}
          pos={[3, 12, 1, 3]}
          id={BetTypeEnum.STRAIGHT_UP_0}
          className="rounded-l-[0.9em]"
        />

        {Array.from({ length: 12 }, (_, i) => i + 1).map((num) => (
          <Fragment key={num}>
            <Lattice
              highlights={highlights}
              pos={[3, 6, num * 3, num * 3 + 3]}
              id={`straight-${String(num * 3 - 2)}`}
            />
            <Lattice
              highlights={highlights}
              pos={[6, 9, num * 3, num * 3 + 3]}
              id={`straight-${String(num * 3 - 1)}`}
            />
            <Lattice
              highlights={highlights}
              pos={[9, 12, num * 3, num * 3 + 3]}
              id={`straight-${String(num * 3)}`}
            />
          </Fragment>
        ))}

        <Lattice
          highlights={isSettlement ? highlights : []}
          pos={[15, 12, 3, 15]}
          id={BetTypeEnum.DOZEN_1}
          className="rounded-tl-[0.9em]"
        />
        <Lattice
          highlights={isSettlement ? highlights : []}
          pos={[15, 12, 15, 27]}
          id={BetTypeEnum.DOZEN_13}
          className=""
        />
        <Lattice
          highlights={isSettlement ? highlights : []}
          pos={[15, 12, 27, 39]}
          id={BetTypeEnum.DOZEN_25}
          className="rounded-tr-[0.9em]"
        />
        <Lattice
          highlights={isSettlement ? highlights : []}
          pos={[3, 6, 39, 42]}
          id={BetTypeEnum.COLUMN_1}
          className="rounded-br-[0.9em]"
        />
        <Lattice
          highlights={isSettlement ? highlights : []}
          pos={[6, 9, 39, 42]}
          id={BetTypeEnum.COLUMN_2}
          className=""
        />
        <Lattice
          highlights={isSettlement ? highlights : []}
          pos={[9, 12, 39, 42]}
          id={BetTypeEnum.COLUMN_3}
          className="rounded-tr-[0.9em]"
        />
      </div>

      <div className="invisibleBoard contents">
        <Lattice
          highlights={highlights}
          pos={[2, 4, 2, 4]}
          id={BetTypeEnum.CORNER_0_1_2_3}
        />
        <Lattice
          highlights={highlights}
          pos={[4, 5, 2, 4]}
          id={BetTypeEnum.SPLIT_0_1}
        />
        <Lattice
          highlights={highlights}
          pos={[5, 7, 2, 4]}
          id={BetTypeEnum.STREET_0_1_2}
        />
        <Lattice
          highlights={highlights}
          pos={[7, 8, 2, 4]}
          id={BetTypeEnum.SPLIT_0_2}
        />
        <Lattice
          highlights={highlights}
          pos={[8, 10, 2, 4]}
          id={BetTypeEnum.STREET_0_2_3}
        />
        <Lattice
          highlights={highlights}
          pos={[10, 11, 2, 4]}
          id={BetTypeEnum.SPLIT_0_3}
        />

        {Array.from({ length: 11 }, (_, i) => i * 3).map((num) => (
          <Lattice
            highlights={highlights}
            key={num}
            pos={[2, 4, num + 5, num + 7]}
            id={`line-${num + 1}`}
          />
        ))}

        {Array.from({ length: 12 }, (_, i) => i * 3 + 4).map((num) => (
          <Fragment key={num}>
            <Lattice
              highlights={highlights}
              pos={[2, 4, num, num + 1]}
              id={`street-${num - 3}_${num - 2}_${num - 1}`}
            />
            <Lattice
              highlights={highlights}
              pos={[5, 7, num, num + 1]}
              id={`split-${num - 3}_${num - 2}`}
            />
            <Lattice
              highlights={highlights}
              pos={[8, 10, num, num + 1]}
              id={`split-${num - 2}_${num - 1}`}
            />
          </Fragment>
        ))}

        {Array.from({ length: 11 }, (_, i) => i * 3 + 5).map((num) => (
          <Fragment key={num}>
            <Lattice
              highlights={highlights}
              pos={[5, 7, num, num + 2]}
              id={`corner-${num - 4}_${num - 3}_${num - 1}_${num}`}
            />
            <Lattice
              highlights={highlights}
              pos={[8, 10, num, num + 2]}
              id={`corner-${num - 3}_${num - 2}_${num}_${num + 1}`}
            />
            <Lattice
              highlights={highlights}
              pos={[4, 5, num, num + 2]}
              id={`split-${num - 4}_${num - 1}`}
            />
            <Lattice
              highlights={highlights}
              pos={[7, 8, num, num + 2]}
              id={`split-${num - 3}_${num}`}
            />
            <Lattice
              highlights={highlights}
              pos={[10, 11, num, num + 2]}
              id={`split-${num - 2}_${num + 1}`}
            />
          </Fragment>
        ))}
      </div>
    </div>
  )
})

const betTypeToGroup = (betType: BetTypeEnum): BetTypeEnum[] => {
  if (ALL_GROUP_MAP[betType]) return ALL_GROUP_MAP[betType]

  return [betType]
}

export default function GridBoard() {
  const bettingBoardArgs = { convertBetMap: convertRouletteMap, getAddChipParams, boardID: BoardIDEnum.GRID_DESKTOP }
  const { handlePointerDown, handlePointerUp, ...dragProps } = useBettingBoard(bettingBoardArgs)

  const isBet = useBettingStore((s) => s.isBet)

  // highlight board logic
  const isMain = !useBettingStore((s) => s.isRaceTrack)
  const highlightArgs = { isMain, prefix: 'straight-', getInitWinHighlight, winHighlightMap: GRID_WIN_HIGHLIGHT_FN }
  const { highlights, setHighlights } = useBoardHighlight(highlightArgs)

  const handleHover = (event: React.MouseEvent<HTMLDivElement>) => {
    if (!isBet) return

    const boardId = (event.target as HTMLSpanElement).id
    if (!boardId) {
      setHighlights([])
      return
    }
    const betType = boardId.slice(betGridPrefix.length) as BetTypeEnum

    let list = betTypeToGroup(betType).map((v) => String(v))
    if (Object.keys(GRID_HOVER_HIGHLIGHT).includes(betType)) list = [betType]
    if ([BetTypeEnum.EVEN, BetTypeEnum.RED, BetTypeEnum.BLACK, BetTypeEnum.ODD].some((type) => type === betType))
      list = list.concat(betType)

    setHighlights(list)
  }

  const handleLeave = () => {
    if (!isBet) return
    setHighlights([])
  }

  return (
    <div
      id="D_GridBoard"
      className="relative"
      onMouseOver={handleHover}
      onMouseLeave={handleLeave}
      onPointerDown={handlePointerDown}
      onPointerUp={handlePointerUp}
    >
      <div
        className={cn(
          'absolute z-[-1] h-full w-full opacity-0 transition-opacity duration-500 ease-in',
          isMain && 'opacity-0 duration-200',
          !isBet && isMain && 'opacity-100 delay-100 duration-500' // Flat to slope
        )}
      >
        <Image
          src={gridBoardTiltShadow}
          className="absolute bottom-[-15%] left-[4%] w-[92%]"
          alt="Grid board tilt shadow"
        />
      </div>
      <Image
        src={grid}
        alt="Grid board"
      />
      <StaticGridBoard highlights={highlights} />
      <TrackerChip
        {...dragProps}
        boardId="D_GridBoard"
      />
    </div>
  )
}
