import SquareButton from '@repo/ui/components/common/button/SquareButton'
import Image from '@repo/ui/components/common/Image'
import { Icon } from '@repo/ui/icon'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useWinAnimStore } from '@repo/ui/store/useWinAnimStore'
import { cn } from '@repo/utils/common/common'
import { playSound } from '@repo/utils/common/sound'
import gridBoardTiltBottom from '@roulette/assets/board/desktop/gridBoardTiltBottom.webp'
import gridBoardTiltTop from '@roulette/assets/board/desktop/gridBoardTiltTop.webp'
import raceTrackTiltBottom from '@roulette/assets/board/desktop/raceTrackTiltBottom.webp'
import raceTrackTiltCenter from '@roulette/assets/board/desktop/raceTrackTiltCenter.webp'
import raceTrackTiltTop from '@roulette/assets/board/desktop/raceTrackTiltTop.webp'
import BetResult from '@roulette/components/header/BetResult'
import { AnimatePresence, motion } from 'framer-motion'
import { useState } from 'react'
import { useTranslation } from 'react-i18next'
import GridBoard from './board/grid/GridBoard'
import RaceTrackBoard from './board/raceTrack/RaceTrackBoard'
import RaceTrackRangeButton from './RaceTrackRangeButton/RaceTrackRangeButton'

const shareVariants = {
  main: {
    bottom: '1.9rem',
    right: '30.2rem',
    width: '39.6rem',
  },
  sub: {
    bottom: '4.1rem',
    right: '2rem',
    width: '25.2rem',
  },
  transition: {
    duration: 0.6,
    ease: 'easeInOut',
  },
}

const gridBoardVariants = {
  ...shareVariants,
  flat: {
    transform: [
      'translate3d(-0.15%, -8.4%, 0) perspective(8rem) rotate3d(1, 0, 0, 4.7deg) scale(1.024, 0.745)',
      'translate3d(0, 0, 0) perspective(8.5rem) rotate3d(1, 0, 0, 0deg) scale(1, 1)',
    ],
    transition: {
      duration: 1,
      ease: [0.47, 0, 0.05, 1],
    },
  },
  slope: {
    transform: [
      'translate3d(0, 0, 0) perspective(8.5rem) rotate3d(1, 0, 0, 0deg) scale(1, 1)',
      'translate3d(-0.15%, -8.4%, 0) perspective(8rem) rotate3d(1, 0, 0, 4.7deg) scale(1.024, 0.745)',
    ],
    transition: {
      duration: 1,
      ease: [0.47, 0, 0.05, 1],
    },
  },
}

const raceTrackBoardVariants = {
  ...shareVariants,
  flat: {
    transform: [
      'translate3d(-0.05rem, -7.6%, 0) perspective(8rem) rotate3d(1, 0, 0, 4.7deg) scale(1.046, 0.76)',
      'translate3d(0, 0, 0) perspective(8.5rem) rotate3d(1, 0, 0, 0deg) scale(1, 1)',
    ],
    transition: {
      duration: 1,
      ease: [0.47, 0, 0.05, 1],
    },
  },
  slope: {
    transform: [
      'translate3d(0, 0, 0) perspective(8.5rem) rotate3d(1, 0, 0, 0deg) scale(1, 1)',
      'translate3d(-0.05rem, -7.6%, 0) perspective(8rem) rotate3d(1, 0, 0, 4.7deg) scale(1.046, 0.76)',
    ],
    transition: {
      duration: 1,
      ease: [0.47, 0, 0.05, 1],
    },
  },
}

const GameContent = () => {
  const [isAnimating, setIsAnimating] = useState(false)
  const { t } = useTranslation()
  const isBet = useBettingStore((s) => s.isBet)
  const desktopMenu = useLayoutStore((s) => s.desktopModal)
  const isRaceTrack = useBettingStore((s) => s.isRaceTrack)
  const animState = useWinAnimStore((s) => s.animState)

  return (
    <div className={cn({ isAnimating })}>
      <AnimatePresence>
        <motion.div
          className="absolute w-full"
          key="gridTilt"
          variants={gridBoardVariants}
          initial={shareVariants.main}
          animate={[!isRaceTrack ? 'main' : 'sub']}
          transition={shareVariants.transition}
        >
          <Image
            key="gridBoardTiltTop"
            src={gridBoardTiltTop}
            className={cn(
              'absolute bottom-[2.69rem] left-[0.04rem] w-[99.55%] opacity-0 transition-opacity duration-200 ease-in',
              !isRaceTrack && 'opacity-0 duration-200',
              !isBet && !isRaceTrack && 'opacity-100 delay-200 duration-500'
            )}
            alt="Grid board tilt top"
          />
          <Image
            key="gridBoardTiltBottom"
            src={gridBoardTiltBottom}
            className={cn(
              'absolute bottom-[0.7rem] left-[6.16%] w-[87.4%] opacity-0 transition-opacity duration-200 ease-in',
              !isRaceTrack && 'opacity-0 duration-200',
              !isBet && !isRaceTrack && 'opacity-100 delay-200 duration-500'
            )}
            alt="Grid board tilt bottom"
          />
        </motion.div>
        <motion.div
          className="rotateBoard absolute z-[1]"
          key="grid"
          variants={gridBoardVariants}
          initial={shareVariants.main}
          animate={[!isRaceTrack ? 'main' : 'sub', !isBet && !isRaceTrack ? 'slope' : 'flat']}
          transition={shareVariants.transition}
        >
          <GridBoard />
        </motion.div>

        {/* raceTrackTilt */}
        <motion.div
          className="absolute w-full"
          key="raceTrackTilt"
          variants={raceTrackBoardVariants}
          initial={shareVariants.main}
          animate={[isRaceTrack ? 'main' : 'sub']}
          transition={shareVariants.transition}
        >
          <Image
            key="raceTrackTiltTop"
            src={raceTrackTiltTop}
            className={cn(
              'absolute bottom-[1.5rem] left-[-0.1%] w-[100%] origin-center opacity-0 transition-all duration-200 ease-linear',
              isRaceTrack && 'bottom-[1.5rem] opacity-0 duration-200',
              !isBet && isRaceTrack && 'bottom-[0.61rem] opacity-100 delay-100 duration-700'
            )}
            alt="Race track tilt center"
          />
          <Image
            key="raceTrackTiltCenter"
            src={raceTrackTiltCenter}
            className={cn(
              'absolute bottom-[0.61rem] left-[-0.1%] w-[100%] origin-center opacity-0 transition-opacity duration-200 ease-linear',
              isRaceTrack && 'opacity-0 duration-200',
              !isBet && isRaceTrack && 'opacity-100 delay-[250ms] duration-700'
            )}
            alt="Race track tilt center"
          />
          <Image
            key="raceTrackTiltBottom"
            src={raceTrackTiltBottom}
            className={cn(
              'absolute bottom-[0.71rem] left-[-0.1%] w-[100%] origin-center opacity-0 transition-all duration-200 ease-linear',
              isRaceTrack && 'bottom-[0.71rem] opacity-0 duration-200',
              !isBet && isRaceTrack && 'bottom-[0.61rem] opacity-100 delay-200 duration-700'
            )}
            alt="Race track tilt bottom"
          />
        </motion.div>
        {/* raceTrack */}
        <motion.div
          className="rotateBoard absolute"
          key="raceTrack"
          variants={raceTrackBoardVariants}
          initial={shareVariants.sub}
          animate={[!isRaceTrack ? 'sub' : 'main', !isBet && isRaceTrack ? 'slope' : 'flat']}
          transition={shareVariants.transition}
        >
          <motion.div
            className="absolute -left-12 top-1/2 -translate-y-1/2"
            transition={{ duration: 0.6 }}
            animate={{ opacity: !isRaceTrack ? 0 : !isBet ? 0 : 1 }}
          >
            <RaceTrackRangeButton />
          </motion.div>

          <RaceTrackBoard showTiltEffect={!isBet && isRaceTrack} />
        </motion.div>
      </AnimatePresence>

      <SquareButton
        child={<Icon.Switch />}
        className={`absolute bottom-[7.18rem] right-[27.445rem] ${animState && 'opacity-50'}`}
        buttonClassName="h-10 w-10 rounded-[0.75rem]"
        isGhost
        tooltipContent={t('COMMON.SWITCH')}
        tooltipPlacement="bottom"
        selected={false}
        onClick={() => {
          if (animState) {
            playSound(['disabledAction'])
            return
          }
          useBettingStore.setState((state) => ({ isRaceTrack: !state.isRaceTrack }))
          setIsAnimating(true)
          setTimeout(() => {
            setIsAnimating(false)
          }, 400)
        }}
      />

      <motion.div
        className="absolute bottom-5 right-[11.75rem]"
        transition={{ duration: 0.6 }}
        animate={{
          opacity: !isRaceTrack ? 1 : 0,
          y: !isRaceTrack ? 0 : '1rem',
        }}
      >
        <RaceTrackRangeButton
          isVertical
          className="w-[5.6rem]"
        />
      </motion.div>

      <BetResult className={`d_resultWrap absolute bottom-[14rem] right-[5.6rem] z-10 ${desktopMenu === 'statistics' && 'noBg'}`} />
    </div>
  )
}

export default GameContent
